/* Style-guide-specific grid styling */
.sg-body .column,
.sg-body .grid_item {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  position: relative;
  text-align: center;
  font-size: 0.9375rem;
}

.sg-body .column:before,
.sg-body .grid_item:before {
  content: "";
  position: absolute;
  display: block;
  width: calc(100% - 2rem);
  left: 2rem;
  top: 0;
  bottom: 0;
  background-color: rgba(13, 73, 123, 0.1);
}

.grid_full .grid_item:before {
  width: 100%;
  left: 0;
}

.grid_wide .grid_item:before {
  width: calc(100% - 4rem);
  left: 4rem;
}

.grid_narrow .grid_item:before {
  width: calc(100% - 1rem);
  left: 1rem;
}

/******Base Layout******/
.sg-body {
  margin: 0;
}

.sg-wrap {
  display: block;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  overflow: auto;
}

.sg-menu {
  position: absolute;
  vertical-align: top;
  background-color: #081D33;
  width: 18rem;
  top: 0;
  left: 0;
  bottom: 0;
  font-size: .9em;
}

.sg-main {
  padding-left: 18rem;
  transition: padding .15s ease-out;
}

.sg-category {
  padding-left: 5%;
  padding-right: 5%;
  overflow: auto;
}

.sg-category_wrap {
  max-width: 58em;
  /* Should be wider for universal box-sizing: border-box */
  margin: auto;
}

.sg-example {
  width: 110%;
  margin-left: -5%;
}

@media only screen and (max-width: 1100px) {
  .sg-menu {
    width: 10rem;
  }
  .sg-main {
    padding-left: 10rem;
  }
}

@media only screen and (max-width: 700px) {
  .sg-menu {
    width: 0;
  }
  .sg-main {
    padding-left: 0;
  }
}

/******Menu******/
.sg-menu {
  transition: all .2s ease-out;
  overflow: auto;
}

.sg_menu_list,
.sg-menu_item {
  padding: 0;
  margin: 0;
  position: relative;
}

.sg-menu_list {
  list-style: none;
  padding: 0;
  color: rgba(245, 209, 221, 0.1);
  /* Used for border colors */
}

.sg-menu_section {
  border-top: 1px solid;
}

.sg-menu_article > .sg-menu_link {
  color: rgba(202, 221, 226, 0.65);
}

.sg-category-active {
  background: rgba(94, 131, 158, 0.04);
}

/* Menu links */
.sg-menu_link {
  display: block;
  padding: .75rem 1.65rem .65rem;
  text-decoration: none;
  color: #CADDE2;
}

.sg-menu_link:hover,
.sg-menu_link:focus {
  background-color: rgba(202, 221, 226, 0.05);
  color: #CADDE2;
}

.sg-toc-active > .sg-menu_link {
  background-color: rgba(245, 209, 221, 0.1);
}

.sg-menu_section > .sg-menu_link {
  color: #f60255;
}

.sg-menu_article .sg-menu_link {
  padding-left: 2.5rem;
  padding-right: 2rem;
}

@media only screen and (max-width: 1100px) {
  .sg-menu_link {
    padding-left: .5rem;
  }
  .sg-menu_article .sg-menu_link {
    padding-left: .75rem;
  }
}

.sg-menu_article {
  display: none;
}

.sg-category-active .sg-menu_article,
.sg-menu_article.sg-toc-active {
  display: block;
}

.sg-category-active > .sg-menu_link {
  border-left: 2px solid #f60255;
  text-indent: -2px;
}

/******Project Logo******/
.sg-logo {
  font-weight: normal;
  background-color: #f60255;
  background-image: url("images/logo.svg");
  background-attachment: fixed;
  background-size: 100vw;
  color: #fff;
  margin: 0;
  font-size: 1.5em;
}

.sg-logo:hover,
.sg-logo:focus {
  background-color: #fd246e;
}

.sg-logo .sg-menu_link {
  color: inherit;
  padding-top: 1em;
  padding-bottom: .75em;
  opacity: 0;
  transition: opacity .3s ease-out;
}

.sg-menu[style*="fixed"] .sg-logo .sg-menu_link {
  opacity: 1;
}

/******Headings******/
.sg-heading-section {
  color: #5E839E;
  font-size: 2.8587rem;
  line-height: 1.57414;
  font-weight: normal;
}

.sg-heading-category {
  color: #175CA2;
  font-weight: normal;
  font-size: 1.6em;
}

.sg-heading-category-article,
.sg-heading-article {
  font-size: 1.60181rem;
  line-height: 1.40466;
}

.sg-heading-article {
  color: #175CA2;
  font-weight: normal;
}

.sg-heading .sg-code {
  color: #435270;
}

.sg-heading-6 {
  font-weight: normal;
}

.sg-heading-5 + .sg-heading-6 {
  margin-top: -1rem;
  padding-bottom: .25rem;
}

/******Section, Category & Article Layout******/
.sg-section:nth-child(even) {
  padding-bottom: 1.5em;
}

.sg-section:first-child {
  padding-top: 10rem;
}

.sg-section:first-child .sg-category {
  border-top: 0;
}

/* Article spacing */
.sg-article_comment,
.sg-example {
  margin-bottom: 1.5rem;
}

.sg-section {
  padding-bottom: 3rem;
}

.sg-section:nth-child(odd) {
  background-color: rgba(224, 229, 235, 0.3);
}

.sg-category:nth-child(odd):last-child {
  margin-bottom: -3rem;
  padding-bottom: 4.5rem;
}

.sg-category + .sg-category {
  background-color: transparent;
  border-top: 1px solid #e0e5eb;
}

.sg-article {
  margin-bottom: 3em;
}

.sg-category,
.sg-article {
  padding-top: 3em;
}

.sg-article_comment {
  margin-top: -1.5rem;
}

.sg-article_head {
  padding-bottom: 1.5em;
}

.sg-example {
  background-color: rgba(255, 255, 255, 0.8);
  border-top: 1px solid #fff;
}

.sg-example:before {
  content: 'Example';
  background-color: #EDF0F2;
  color: #647582;
  display: inline-block;
  font-size: .9em;
  padding: .45em .5em;
  margin-bottom: .5em;
  margin-left: 1px;
  clear: both;
}

.sg-example_wrap {
  padding: .75em 1.25em;
  position: relative;
}

/* Clearfix */
.sg-example_wrap:after {
  content: " ";
  display: table;
  height: 0;
  width: 100%;
}

.sg-example_wrap:after {
  clear: both;
}

.sg-markup_wrap {
  margin: 0;
}

.sg-markup-block {
  border-top: 1px solid #E0ECF0;
}

.sg-codespan {
  color: #5E839E;
}

.sg-file {
  color: #5E839E;
}

.sg-codeblock {
  margin-bottom: 1.5rem;
}

.sg-code-meta + .sg-code-meta {
  margin-top: -1.5rem;
}

.sg-code-meta:before {
  content: attr(data-code-meta) ": ";
  text-transform: capitalize;
}

/* Highlight.js Code Blocks */
#styleguide .sg-markup_wrap,
#styleguide .sg-markup code {
  overflow: auto;
}

#styleguide .sg-markup_wrap {
  max-height: 30em;
}

#styleguide .sg-markup code {
  display: block;
  word-wrap: normal;
  white-space: pre;
  padding: 1em;
}

/* Custom background color to match nav. Overwrites highlight.js default */
#styleguide .hljs {
  background-color: #40476B;
  font-size: .9em;
}

#styleguide .hljs-comment {
  color: #8aaeb7;
}

code {
  font-family: 'Roboto Mono', monospace;
}
